/////////////button categories///////
.newsHP-categories {
  position: relative;
  overflow: hidden;
  z-index: 1;
  transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
.newsHP-categories:after {
  content: '';
  position: absolute;
  display: block;
  top: 0;
  left: 50%;
  -webkit-transform: translateX(-50%);
  transform: translateX(-50%);
  width: 0;
  height: 100%;
  background: var(--accent-color);
  z-index: -1;
  transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
.newsHP-categories:hover:after {
  width: 110%;
  transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
