///link///////

.btn-footer:link,
.btn-footer:visited {
  transition: all 0.2s;
  position: absolute;
}

.btn-footer:hover {
  transform: translateY(-3px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
}

.btn-footer:active {
  transform: translateY(-1px);
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
}

.btn-footer::after {
  content: '';
  display: inline-block;
  height: 100%;
  width: 100%;
  border-radius: 100px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  transition: all 0.4s;
}

.btn-footer:hover::after {
  transform: scaleX(1.4) scaleY(1.6);
  opacity: 0;
}
